import { useContext, useEffect, useReducer, Children, cloneElement } from "react";
import { CarouselContext } from "pure-react-carousel";

function pct(num) {
    return `${num}%`;
}

export function PureThumbnails({ 
    naturalHeight,
    naturalWidth,
    orientation = "horizontal",
    children,
    rtl = false,
    horizontalPixelThreshold = 15,
    verticalPixelThreshold = 10,
    visibleSlides = 1,
    disableAnimation = false,
    ...otherProps
}) {
    const carouselContext = useContext(CarouselContext);
    const [, forceUpdate] = useReducer(x => x + 1, 0); 

    useEffect(() => {
        function onChange() {
            forceUpdate();
        }
        carouselContext.subscribe(onChange);
        return () => carouselContext.unsubscribe(onChange);
    }, [carouselContext]);
    
    const slideTraySize = Math.max(100 * carouselContext.state.totalSlides / visibleSlides, 100);
    const slideSizes = (100 / Math.max(carouselContext.state.totalSlides, visibleSlides));
    // slider tray wrap
    const trayWrapStyle = {};

    if (orientation === 'vertical') {
      trayWrapStyle.height = 0;
      trayWrapStyle.paddingBottom = pct(
        (naturalHeight * 100 * visibleSlides) / naturalWidth,
      );
      trayWrapStyle.width = pct(100);
    }

    // slider tray
    const trayStyle = {};
    const trans = pct( slideSizes * Math.max(0, Math.min(visibleSlides > 1 ? carouselContext.state.currentSlide - 1 : carouselContext.state.currentSlide, carouselContext.state.totalSlides - visibleSlides)) * -1);

    if (orientation === 'vertical') {
      trayStyle.transform = `translateY(${trans})`;
      trayStyle.width = pct(100);
      trayStyle.flexDirection = 'column';
    } else {
      trayStyle.width = pct(slideTraySize);
      trayStyle.transform = `translateX(${trans})`;
      trayStyle.flexDirection = 'row';
    }

    return (
        <div className={["carousel__thumbs", orientation === 'vertical' ? 'carousel__thumbs--vertical' : 'carousel__thumbs--horizontal'].join(' ')}>
            <div className="carousel__thumbs__wrapper"
                style={trayWrapStyle}
            >
                <div className="carousel__thumbs__tray" 
                    style={trayStyle}
                >
                    {
                        Children.map(children, (child, index) => {
                            return cloneElement(child, {
                                ...child.props, 
                                className: carouselContext.state.currentSlide === index ? 'carousel__thumbs__slide--selected' : '',
                                aspectRatio: orientation === 'vertical' ? (naturalHeight / naturalWidth) : undefined,
                                style: {
                                    width: orientation === 'vertical' ? pct(100) : pct(slideSizes),
                                },
                            })
                        })
                    }
                </div>
            </div>
        </div>
    )
}

export function PureThumb({
    className,
    style,
    index,
    aspectRatio,
    children,
}) {
    const carouselContext = useContext(CarouselContext);

    const handleClick = () => {
        carouselContext.setStoreState({
            currentSlide: index,
        })
    }
    return (
        <button className={["carousel__thumbs__slide", aspectRatio && 'carousel__thumbs__slide--vertical' , className].join(' ')} 
            style={style}
            onClick={handleClick}
        >
            {aspectRatio &&  <div style={{paddingTop: pct(aspectRatio * 100)}} className="carousel__thumbs__slide-aspect"></div> }
            <div className="carousel__thumbs__slide-wrapper">
                {children}
            </div>
        </button>
    );
}