import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from "pure-react-carousel";
import { useMediaQuery } from "usehooks-ts";

import { useGetStoriesQuery } from "~entities/shop";
import { SVG_ARROW_LEFT, SVG_ARROW_RIGHT } from "~shared/lib";

function StoriesItem({ imgSrc, name, placeholder = false }) {
  const classes = ["section-stories__story"];
  if (placeholder) {
    classes.push("section-stories__story--placeholder");
    return (
      <div className={classes.join(" ")}>
        <div className="section-stories__story__img"></div>
        <div className="section-stories__story__title"></div>
      </div>
    );
  }

  return (
    <div className={classes.join(" ")}>
      <div className="section-stories__story__img">
        <img src={imgSrc} alt="" loading="lazy" />
      </div>
      <div className="section-stories__story__title">{name}</div>
    </div>
  );
}

export default function SectionStories({ className }) {
  const { data: stories, isSuccess } = useGetStoriesQuery();

  const matchesLg = useMediaQuery("(min-width: 992px)");
  const matchesXl = useMediaQuery("(min-width: 1200px)");

  const countVisibleSlide = matchesXl ? 8 : matchesLg ? 6 : 4;

  let renderContent;
  if (isSuccess) {
    renderContent = stories.map((data, index) => {
      return (
        <Slide key={data.id} index={index}>
          <StoriesItem imgSrc={data.image} name={data.title} />
        </Slide>
      );
    });
  } else {
    renderContent = Array.from(Array(8), (_, index) => {
      return (
        <Slide key={index} index={index}>
          <StoriesItem placeholder={true} />
        </Slide>
      );
    });
  }
  return (
    <section
      className={[
        "section",
        "section-padding",
        "section-stories",
        className,
      ].join(" ")}
    >
      <CarouselProvider
        className="container"
        naturalSlideHeight={100}
        naturalSlideWidth={100}
        totalSlides={renderContent.length}
        visibleSlides={countVisibleSlide}
        isIntrinsicHeight={true}
      >
        <Slider>{renderContent}</Slider>
        <div className="carousel__controls">
          <ButtonBack>{SVG_ARROW_LEFT}</ButtonBack>
          <DotGroup showAsSelectedForCurrentSlideOnly={true} />
          <ButtonNext>{SVG_ARROW_RIGHT}</ButtonNext>
        </div>
      </CarouselProvider>
    </section>
  );
}
