import { Provider } from 'react-redux';
import { configureStore } from "@reduxjs/toolkit";

import { apiShop, currentOrderSlice } from "~entities/shop";

const store = configureStore({
    reducer: {
        [apiShop.reducerPath]: apiShop.reducer, 
        [currentOrderSlice.name]: currentOrderSlice.reducer,
    },
    middleware: getDefaultMiddleware => getDefaultMiddleware().concat(apiShop.middleware),
    devTools: true,
})

console.log(currentOrderSlice);

export function ReduxProvider({ children }) {
    return <Provider store={store}>
        {children}
    </Provider>
}