import { Link } from "react-router-dom";

import { convertNumToPrice } from "~shared/lib";

export function ProductCartVertical({
  linkTo,
  imgSrc,
  name,
  price,
  isSale = false,
  isChangeFavorite,
  priceOld,
  placeholder = false,
  className,
}) {
  const classes = ["product-cart-vertical", className];

  if (placeholder) {
    classes.push("product-cart-vertical--placeholder");
    return (
      <Link className={classes.join(" ")}>
        <div className="product-cart-vertical__img"></div>
        <div className="product-cart-vertical__name"></div>
        <div className="product-cart-vertical__price">
          <div className="product-cart-vertical__price__current"></div>
        </div>
      </Link>
    );
  }

  if (isChangeFavorite) classes.push("product-cart-vertical--favorite");
  if (isSale) classes.push("product-cart-vertical--sale");

  return (
    <Link to={linkTo} className={classes.join(" ")}>
      <div className="product-cart-vertical__img">
        <img src={imgSrc} alt="" loading="lazy" />
        {isChangeFavorite ? (
          <svg
            width="19"
            height="17"
            viewBox="0 0 19 17"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M17.2804 1.76207C16.2665 0.74543 14.9272 0.129421 13.5043 0.0251955C12.0814 -0.0790302 10.6686 0.335391 9.52079 1.19368C8.31087 0.281986 6.8049 -0.13142 5.30615 0.036713C3.80741 0.204846 2.42721 0.942028 1.44349 2.09981C0.45978 3.25759 -0.0543779 4.74997 0.00456013 6.27642C0.0634982 7.80287 0.691154 9.25001 1.76113 10.3264L7.46676 16.1548C8.00167 16.696 8.72675 17 9.48275 17C10.2388 17 10.9638 16.696 11.4987 16.1548L17.2044 10.3264C17.7663 9.7702 18.2142 9.10698 18.5223 8.37485C18.8304 7.64272 18.9927 6.85612 18.9998 6.06026C19.0068 5.2644 18.8586 4.47497 18.5635 3.73734C18.2685 2.99971 17.8324 2.32843 17.2804 1.76207ZM15.9396 8.94879L10.234 14.729C10.1456 14.8193 10.0404 14.891 9.92454 14.9399C9.80865 14.9888 9.68436 15.014 9.55883 15.014C9.43329 15.014 9.309 14.9888 9.19312 14.9399C9.07724 14.891 8.97206 14.8193 8.88366 14.729L3.17803 8.94879C2.43226 8.1765 2.01466 7.13907 2.01466 6.05869C2.01466 4.9783 2.43226 3.94087 3.17803 3.16858C3.93798 2.40848 4.96292 1.98226 6.03085 1.98226C7.09877 1.98226 8.12371 2.40848 8.88366 3.16858C8.97206 3.25888 9.07724 3.33055 9.19312 3.37946C9.309 3.42837 9.43329 3.45355 9.55883 3.45355C9.68436 3.45355 9.80865 3.42837 9.92454 3.37946C10.0404 3.33055 10.1456 3.25888 10.234 3.16858C11.0145 2.5394 11.9963 2.22326 12.9921 2.28045C13.988 2.33764 14.9282 2.76417 15.6335 3.47869C16.3388 4.19322 16.7598 5.14574 16.8163 6.1546C16.8728 7.16347 16.5607 8.15809 15.9396 8.94879Z"
              fill="white"
            />
          </svg>
        ) : null}
      </div>
      <div className="product-cart-vertical__name">{name}</div>
      <div className="product-cart-vertical__price">
        {isSale ? (
          <div className="product-cart-vertical__price__old">
            {convertNumToPrice(priceOld)} Р
          </div>
        ) : null}
        <div className="product-cart-vertical__price__current">
          {convertNumToPrice(price)} Р
        </div>
      </div>
    </Link>
  );
}
