import axios from "axios";
import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

import { createSlug } from "~shared/lib/tools";

export const apiShop = createApi({
  reducerPath: "shop",
  baseQuery: fetchBaseQuery({ baseUrl: "https://api.escuelajs.co/api/v1" }),
  endpoints: (builder) => ({
    getProducts: builder.query({
      async queryFn() {
        try {
          let products = await axios(
            "https://api.escuelajs.co/api/v1/products"
          );
          products = products.data.filter((_, index) => index < 20);

          if (products[0]) products[0].isSale = true;
          if (products[5]) products[5].isSale = true;
          if (products[10]) products[10].isSale = true;
          if (products[15]) products[15].isSale = true;

          return {
            data: products,
          };
        } catch (error) {
          return { error };
        }
      },
    }),
    getCategores: builder.query({
      async queryFn() {
        try {
          const categories = await axios(
            "https://api.escuelajs.co/api/v1/categories"
          );
          const keyUnique = {};
          return {
            data: categories.data
              .filter((_, index) => index < 6)
              .filter((item) => {
                if (keyUnique[createSlug(item.name)] === true) return false;
                keyUnique[createSlug(item.name)] = true;
                return true;
              }),
          };
        } catch (error) {
          return { error };
        }
      },
    }),
    getStories: builder.query({
      async queryFn() {
        try {
          const stories = await axios(
            "https://api.escuelajs.co/api/v1/products"
          );
          return {
            data: stories.data
              .filter((_, index) => index < 10)
              .map((data) => {
                // Test: 0
                return {
                  id: data.id,
                  title: data.title.slice(0, 20),
                  image: data.images[0],
                };
              }),
          };
        } catch (error) {
          return { error };
        }
      },
    }),
  }),
});

export const { useGetProductsQuery, useGetCategoresQuery, useGetStoriesQuery } =
  apiShop;
