import { BrowserRouter, Routes, Route } from 'react-router-dom';

import { StandartLayoutRoute } from '~pages/layouts';
import { HomePageRoute } from '~pages/home';
import { CatalogPageRoute } from '~pages/catalog';
import { ProductPageRoute } from '~pages/product';

// import { ROUTES as ROUTES_PATH } from '~shared/lib';

export function RouterProvider() {
    return (
    <BrowserRouter basename={process.env.REACT_APP_PUBLIC_URL}>
        <Routes>
            <Route {...StandartLayoutRoute}>
                <Route {...HomePageRoute} />
                <Route {...CatalogPageRoute} />
                <Route {...ProductPageRoute} />
            </Route>
        </Routes>
    </BrowserRouter>
    );
}