export default function SectionBannerMain({ className }) {
  return (
    <section className={["section", "section-banner", className].join(" ")}>
      <div className="container">
        <Link to={ROUTES.CATALOG} className="section-banner__content">
          {/* {SVG_BACKGROUND} */}
          <div className="section-banner__content__title">
            перейти <br />в каталог
            {SVG_MARK_LINK}
          </div>
        </Link>
      </div>
      <img
        className="section-banner__decor"
        src={process.env.REACT_APP_PUBLIC_URL + "/section-1.png"}
        alt=""
      />
    </section>
  );
}
