import { Link } from "react-router-dom";
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from "pure-react-carousel";

import { useGetCategoresQuery } from "~entities/shop";

import {
  ROUTES,
  createSlug,
  SVG_ARROW_LEFT,
  SVG_ARROW_RIGHT,
  SVG_MARK_LINK,
} from "~shared/lib";
import { useMediaQuery } from "usehooks-ts";

export default function SectionListCategories({
  className,
  title = "Каталог",
}) {
  const { data: categories, isSuccess } = useGetCategoresQuery();

  const matchesSm = useMediaQuery("(min-width: 600px)");
  const matchesLg = useMediaQuery("(min-width: 992px)");

  let renderSlides;
  const renderIconLink = (
    <div className="section-catalog__carousel-icon">{SVG_MARK_LINK}</div>
  );
  if (isSuccess) {
    renderSlides = categories.map((data, index) => {
      return (
        <Slide key={data.id} index={index}>
          <Link to={ROUTES.CATALOG + "/" + createSlug(data.name)}>
            <div className="section-catalog__carousel-title">{data.name}</div>
            <img
              className="section-catalog__carousel-image"
              src={data.image}
              alt={data.alt}
              loading="lazy"
            />
            {renderIconLink}
          </Link>
        </Slide>
      );
    });
  } else {
    renderSlides = [
      <Slide key={0} index={0}>
        {renderIconLink}
      </Slide>,
      <Slide key={1} index={1}>
        {renderIconLink}
      </Slide>,
      <Slide key={2} index={2}>
        {renderIconLink}
      </Slide>,
    ];
  }

  return (
    <section
      className={[
        "section",
        "section-padding",
        "section-catalog",
        className,
      ].join(" ")}
    >
      <h2 className="container section__title">{title}</h2>
      <CarouselProvider
        className="container"
        naturalSlideHeight={557}
        naturalSlideWidth={380}
        totalSlides={renderSlides.length}
        visibleSlides={matchesLg ? 3 : matchesSm ? 2 : 1}
      >
        <Slider>{renderSlides}</Slider>
        <div className="carousel__controls">
          <ButtonBack>{SVG_ARROW_LEFT}</ButtonBack>
          <DotGroup showAsSelectedForCurrentSlideOnly={true} />
          <ButtonNext>{SVG_ARROW_RIGHT}</ButtonNext>
        </div>
      </CarouselProvider>
    </section>
  );
}
