import { createSlice } from "@reduxjs/toolkit";

export const currentOrderSlice = createSlice({
    name: 'currentOrder',
    initialState: [],
    reducers: {
        set: (state, action) => {
            return action.payload;
        },
        add: (state, action) => {
            state.push(action.payload);
        },
        remove: (state, action) => {
            state.pop();
        }
    }
})

export const { set, add, remove } = currentOrderSlice.actions;

export const selectLength = state => state.currentOrder.length;