import { Outlet, useLocation } from "react-router-dom";

import { Header } from "~widgets/header";
import { Footer } from "~widgets/footer";

import { useScrollPosition } from "./useScrollPosition";

function StandartLayout() {
  const { pathname } = useLocation();
  const positionY = useScrollPosition();

  const handleClick = () => {
    if (window) {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    }
  };

  return (
    <>
      <Header isReverse={pathname === "/" ? true : false} />
      <main>
        <Outlet />
      </main>
      <div className="fixed-buttons">
        <button
          className={[
            "scroll-button",
            positionY > 150 ? "scroll-button--show" : "",
          ].join(" ")}
          onClick={handleClick}
        >
          <svg
            width="96"
            height="96"
            viewBox="0 0 96 96"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <circle cx="48" cy="48" r="48" fill="#5C5681" />
            <path
              d="M48.9658 21.6507C48.1686 21.3144 47.2771 21.305 46.4867 21.6247C46.0948 21.7789 45.7385 22.009 45.4381 22.3022L22.7249 45.0153C22.1225 45.6177 21.7892 46.4398 21.7983 47.3008C21.8073 48.1618 22.158 48.9911 22.7732 49.6063C23.3884 50.2215 24.2178 50.5722 25.0787 50.5813C25.9397 50.5903 26.7618 50.257 27.3642 49.6546L44.6035 32.4153L44.9814 70.4971C44.9841 70.9256 45.0717 71.3511 45.2391 71.7491C45.4064 72.1471 45.6502 72.5096 45.9565 72.8158C46.2627 73.1221 46.6252 73.3659 47.0232 73.5332C47.4212 73.7006 47.8467 73.7882 48.2753 73.7909C48.7038 73.7972 49.1276 73.7186 49.5222 73.5595C49.9167 73.4005 50.2742 73.1643 50.574 72.8644C50.8739 72.5646 51.1101 72.2071 51.2691 71.8126C51.4282 71.418 51.5068 70.9942 51.5005 70.5657L51.0767 32.4835L68.6828 50.0895C69.298 50.7047 70.1273 51.0555 70.9883 51.0645C71.8492 51.0736 72.6713 50.7402 73.2737 50.1379C73.8761 49.5355 74.2095 48.7134 74.2004 47.8524C74.1913 46.9914 73.8406 46.1621 73.2254 45.5469L50.029 22.3505C49.7223 22.051 49.3611 21.8132 48.9658 21.6507Z"
              fill="#F8F8F8"
            />
          </svg>
        </button>
        <button
          className={[
            "wats-app-widget",
            positionY > 150 ? "wats-app-widget--show" : "",
          ].join(" ")}
        >
          <svg
            width="96"
            height="96"
            viewBox="0 0 96 96"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <circle cx="48" cy="48" r="48" fill="#4CD702" />
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M62.0146 53.356C61.2806 52.9903 57.672 51.223 56.999 50.9792C56.3263 50.7355 55.8371 50.6136 55.3477 51.3449C54.8584 52.0762 53.4517 53.7216 53.0235 54.2092C52.5954 54.6967 52.1673 54.7578 51.4333 54.392C50.6993 54.0264 48.3343 53.2549 45.5307 50.7661C43.3487 48.8293 41.8756 46.4368 41.4473 45.7055C41.0192 44.9742 41.4018 44.5789 41.7693 44.2146C42.0994 43.8875 42.5032 43.3615 42.8702 42.9349C43.2372 42.5083 43.3596 42.2036 43.6042 41.7161C43.8489 41.2285 43.7266 40.8019 43.543 40.4363C43.3596 40.0706 41.8917 36.475 41.28 35.0124C40.6843 33.5879 40.0791 33.7807 39.6285 33.7583C39.2009 33.7371 38.7111 33.7326 38.2218 33.7326C37.7324 33.7326 36.9373 33.9155 36.2644 34.6467C35.5916 35.378 33.6955 37.1455 33.6955 40.741C33.6955 44.3366 36.3256 47.8103 36.6926 48.2978C37.0596 48.7854 41.8684 56.1641 49.2314 59.3283C50.9826 60.0809 52.3499 60.5303 53.4158 60.8672C55.1742 61.423 56.7743 61.3447 58.039 61.1566C59.4491 60.9468 62.3817 59.3892 62.9933 57.6829C63.6049 55.9765 63.6049 54.5139 63.4214 54.2092C63.238 53.9045 62.7487 53.7216 62.0146 53.356ZM48.6222 71.5558H48.6123C44.2311 71.5541 39.934 70.3828 36.1853 68.1688L35.2937 67.642L26.0528 70.0547L28.5194 61.0877L27.9388 60.1684C25.4947 56.2994 24.2039 51.8277 24.2058 47.2362C24.2111 33.8382 35.1642 22.938 48.6319 22.938C55.1534 22.9405 61.2837 25.4715 65.8935 30.0645C70.5033 34.6576 73.0406 40.7629 73.0381 47.2559C73.0326 60.6549 62.0797 71.5558 48.6222 71.5558ZM69.4023 26.5739C63.8562 21.0477 56.4805 18.0029 48.6222 17.9998C32.4308 17.9998 19.2529 31.1142 19.2464 47.2339C19.2442 52.3867 20.5969 57.4166 23.1674 61.85L19 77L34.5726 72.9346C38.8632 75.2637 43.694 76.4911 48.6105 76.4932H48.6225H48.6227C64.8125 76.4932 77.9916 63.3771 77.9982 47.2572C78.0012 39.4454 74.9486 32.0999 69.4023 26.5739Z"
              fill="white"
            />
          </svg>
        </button>
      </div>
      <Footer />
    </>
  );
}

export const StandartLayoutRoute = {
  path: "/",
  element: <StandartLayout />,
};
