import { useState, useRef, useEffect } from "react";

import { SVG_FOR_TOGGLE_HEADER } from "~shared/lib";

export function ToggleHeaderAuto({initOpen = false, onToggleShow, ...props}) {
    const [status, setStatus] = useState(initOpen ? 'initOpen' : 'initClose');

    function handleToggleShow(state) {
        switch(status) {
            case 'initClose':
            case 'close': {
                setStatus('open');
                break;
            }
            case 'initOpen':
            case 'open': {
                setStatus('close');
                break;
            }
            default: 
               break;
        }
        onToggleShow && onToggleShow(state);
    } 

    return <ToggleHeader {...props} onToggleShow={handleToggleShow} status={status} /> 
}

export function ToggleHeader({ 
    title, 
    desc, 
    status, // initClose, initOpen, open, close
    onToggleShow // callback(closing | opening) => void
}) {
    const text = useRef();
    useAnimatingToggle(status, text);

    const isInitClose = status === 'initClose', 
        isInitOpen = status === 'initOpen',
        isOpen = status === 'open' || isInitOpen;

    const classes = ["section-product-info__desc__item"];


    if(isInitOpen) classes.push('section-product-info__desc__item--init-open');
    if(isInitClose) classes.push('section-product-info__desc__item--init-close');
    if(isOpen) classes.push('section-product-info__desc__item--active');

    const handleToggleShow = () => {
        onToggleShow && onToggleShow(isOpen ? 'closing' : 'opening');
    };

    return (
        <div 
            className={classes.join(' ')} 
        >
            <button className="section-product-info__desc__title" onClick={handleToggleShow}>{title}</button>
            <div className="section-product-info__desc__text" ref={text}><p>{desc}</p></div>
            {SVG_FOR_TOGGLE_HEADER}
        </div>
    )
}

const DURATION_ANIMATION = 250; // from ProductInfo.css
function useAnimatingToggle(status, element) {
    const timeoutId = useRef(); 

    useEffect(() => {
        window.clearTimeout(timeoutId.current);
        switch(status) {
            case 'initClose': {
                element.current.style.height = '0px';
                break;
            }
            case 'close': {
                element.current.style.height = element.current.scrollHeight + 'px';
                timeoutId.current = setTimeout(() => {
                    element.current.style.height = '0px';
                }, 0)
                break;
            }
            case 'initOpen': {
                break;
            }
            case 'open': {
                element.current.style.height = element.current.scrollHeight + 'px';
                timeoutId.current = setTimeout( () => {
                    element.current.style.height = null;
                }, DURATION_ANIMATION);
                break; 
            }
            default: {
                break;
            }
        }
    }, [status, element])
}