import { NavLink, Link } from "react-router-dom";

import { ROUTES, formatPhoneNum, LINK_VK, LINK_TG, PHONE_NUM, SVG_TG_ACCENT, SVG_VK_ACCENT } from "~shared/lib";

const currentYear = new Date().getFullYear(); // TODO: Maybe need pure 

export default function Footer() {
    return (
        <div className="footer">
            <div className="footer__container container">
                <nav className="footer__row-1">
                    <Link to={ROUTES.HOME} className="footer__row-1__logo">
                        <img src={process.env.REACT_APP_PUBLIC_URL + "/logos/logo-accent.svg"} alt="Logo woowto" />
                    </Link>
                    <ul className="footer__row-1__list">
                        <li className="footer__row-1__item"><NavLink to={ROUTES.CATALOG}>каталог</NavLink></li>
                        <li className="footer__row-1__item"><NavLink to={ROUTES.SALES}>sales</NavLink></li>
                        <li className="footer__row-1__item"><NavLink to={ROUTES.ABOUT}>о нас</NavLink></li>
                        <li className="footer__row-1__item"><NavLink to={ROUTES.DELIVERY}>доставка и оплата</NavLink></li>
                        <li className="footer__row-1__item"><NavLink to={ROUTES.CARE}>уход</NavLink></li>
                        <li className="footer__row-1__item"><NavLink to={ROUTES.SERTIFICATS}>сертификаты</NavLink></li>
                    </ul>
                </nav>
                <div className="footer__row-2">
                    <p>для связи с нами:</p>
                    <ul className="footer__row-2__list">
                        <li className="footer__row-2__item"><a href={LINK_TG} target="_blank" rel="noopener noreferrer">{SVG_TG_ACCENT}</a></li>
                        <li className="footer__row-2__item"><a href={LINK_VK} target="_blank" rel="noopener noreferrer">{SVG_VK_ACCENT}</a></li>
                    </ul>
                    <p>телефон: <a href={"tel:+" + PHONE_NUM}>{formatPhoneNum(PHONE_NUM, 'standart')}</a></p>
                </div>
                <div className="footer__row-3">
                    <p><NavLink to={ROUTES.PRIVACY} target="_blank">Политика конфиденциальности</NavLink></p>
                    <p>© Все права защищены {currentYear}</p>
                    <p><NavLink to={ROUTES.OFFERDOC} target="_blank">Договор оферты</NavLink></p>
                </div>
            </div>
        </div>
    )
}