import { useLayoutEffect, useRef, useState } from "react";

export default function SectionRunLine({ className, text }) {
    const line = useRef();
    const item = useRef();
    const [lengthClone, setLengthClone] = useState(0);

    useLayoutEffect(() => {
        const handle = () => {
            const widthItem = item.current.offsetWidth;
            const widthLine = line.current.offsetWidth;
    
            setLengthClone(Math.ceil(widthLine / widthItem) + 1);
        }
        handle();
        window.addEventListener('resize', handle);
        return () => window.removeEventListener('resize', handle);
    }, [])

    let renderContent = [
        <div key={-1} className="section-run-line__line__item" ref={item}>{text}</div>
    ]
    renderContent = renderContent.concat(Array.from(Array(lengthClone), (_, index) => {
        return <div key={index} className="section-run-line__line__item">{text}</div>
    }))

    return (<>
        <section className={["section", "section-run-line", className].join(' ')}>
            <div className="section-run-line__line" ref={line}>
                <div className="section-run-line__line__wrapper" style={{
                    animationDuration: (renderContent.length * 5) + 's',
                }}>
                    {renderContent}
                </div>
                <div className="section-run-line__line__wrapper" style={{
                    animationDuration: (renderContent.length * 5) + 's',
                }}>
                    {renderContent}
                </div>
                <div className="section-run-line__line__wrapper" style={{
                    animationDuration: (renderContent.length * 5) + 's',
                }}>
                    {renderContent}
                </div>
            </div>
        </section>
    </>)
}