export function convertNumToPrice(num) {
    let str = String(Math.trunc(num));
    return str.replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1.')
} 

export function createSlug(str) {
    return str.trim().toLowerCase().replaceAll(' ', '-').replaceAll("'", '')
}

export function formatPhoneNum(str, format) {
    const matchStr = str.match(/(\d)(\d{3})(\d{3})(\d{2})(\d{2})/);
    switch(format) {
        case 'standart':
            return `+7 (${matchStr[2]}) ${matchStr[3]}-${matchStr[4]}-${matchStr[5]}`;
        default: 
            return str;
    }
}