import { NavLink, useParams } from "react-router-dom";

import { useGetCategoresQuery, useGetProductsQuery } from "~entities/shop";
import { ProductCartVertical } from "~entities/product";

import { ROUTES, createSlug } from "~shared/lib";

export default function SectionListProducts({ className, title = "Каталог" }) {
  return (
    <section
      className={[
        "section",
        "section-padding",
        "section-catalog-list",
        className,
      ].join(" ")}
    >
      <h2 className="section__title container">{title}</h2>
      <CategoriesRow />
      <SectionProducts />
    </section>
  );
}

function CategoriesRow() {
  useParams(); // for re-render CategoryItem then change params
  const { data: categories, isSuccess } = useGetCategoresQuery();

  let renderCategories;
  if (isSuccess) {
    renderCategories = categories.map((data) => {
      return (
        <CategoryItem
          key={data.id}
          linkTo={ROUTES.CATALOG + "/" + createSlug(data.name)}
          name={data.name}
        />
      );
    });
  } else {
    renderCategories = Array.from(Array(5), (_, index) => {
      return <CategoryItem key={index} placeholder={true} />;
    });
  }

  return (
    <div className="section-catalog-list__category container">
      <div className="section-catalog-list__category__wrapper">
        {renderCategories}
      </div>
    </div>
  );
}

function CategoryItem({ name, linkTo, placeholder = false }) {
  const classes = ["section-catalog-list__category__item"];

  if (placeholder) {
    classes.push("section-catalog-list__category__item--placeholder");
    return (
      <NavLink
        className={() => {
          return classes.join(" ");
        }}
      />
    );
  }

  return (
    <NavLink
      to={linkTo}
      className={({ isActive }) => {
        isActive &&
          classes.push("section-catalog-list__category__item--active");
        return classes.join(" ");
      }}
    >
      {name}
    </NavLink>
  );
}

function SectionProducts() {
  const { slugCategory } = useParams();
  const { data: products, isSuccess } = useGetProductsQuery();

  let renderProducts;
  if (isSuccess) {
    renderProducts = products
      .filter((data) => {
        if (slugCategory === undefined) return true;
        return createSlug(data.category.name) === slugCategory;
      })
      .map((data, index) => {
        return (
          <ProductCartVertical
            key={data.id}
            className="section-catalog-list__list__item"
            linkTo={ROUTES.PRODUCT + "/" + createSlug(data.title)}
            isChangeFavorite={true}
            isSale={data.isSale}
            imgSrc={data.images[0]}
            name={data.title}
            price={data.price * 100}
            priceOld={data.price * 150}
          />
        );
      });

    if (renderProducts.length === 0) {
      renderProducts = (
        <div className="section-catalog-list__list__notice">
          Извините, но в этой категории еще нет товаров
        </div>
      );
    }
  } else {
    renderProducts = Array.from(Array(6), (_, index) => {
      return (
        <ProductCartVertical
          key={index}
          className={"section-catalog-list__list__item"}
          placeholder={true}
        />
      );
    });
  }

  return (
    <div className="section-catalog-list__list container">
      <div className="section-catalog-list__list__wrapper">
        {renderProducts}
      </div>
    </div>
  );
}
