import { Link, useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from "pure-react-carousel";

import { add } from "~entities/shop";
import { PureThumbnails, PureThumb } from "~entities/pure-carousel";

import {
  ROUTES,
  convertNumToPrice,
  SVG_ARROW_LEFT,
  SVG_ARROW_RIGHT,
} from "~shared/lib";
import { ToggleHeaderAuto } from "~shared/ui";
import { useMediaQuery } from "usehooks-ts";

function Carousel() {
  const matches = useMediaQuery("(min-width: 576px)");
  return (
    <CarouselProvider
      className="container section-product-info__left__carousel"
      naturalSlideHeight={691}
      naturalSlideWidth={522}
      totalSlides={7}
      visibleSlides={1}
    >
      <Slider>
        <Slide index={0}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </Slide>
        <Slide index={1}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </Slide>
        <Slide index={2}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </Slide>
        <Slide index={3}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </Slide>
        <Slide index={4}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </Slide>
        <Slide index={5}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </Slide>
        <Slide index={6}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </Slide>
      </Slider>
      <div className="carousel__controls">
        <ButtonBack>{SVG_ARROW_LEFT}</ButtonBack>
        <DotGroup showAsSelectedForCurrentSlideOnly={true} />
        <ButtonNext>{SVG_ARROW_RIGHT}</ButtonNext>
      </div>

      <PureThumbnails
        naturalHeight={117}
        naturalWidth={90}
        visibleSlides={5}
        orientation={matches ? "vertical" : "horizontal"}
        // gup
      >
        <PureThumb index={0}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </PureThumb>
        <PureThumb index={1}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </PureThumb>
        <PureThumb index={2}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </PureThumb>
        <PureThumb index={3}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </PureThumb>
        <PureThumb index={4}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </PureThumb>
        <PureThumb index={5}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </PureThumb>
        <PureThumb index={6}>
          <img src={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"} alt="" />
        </PureThumb>
      </PureThumbnails>
    </CarouselProvider>
  );
}

export default function SectionProductInfo({ className }) {
  useParams();

  const dispatch = useDispatch();

  return (
    <section
      className={[
        "section",
        "section-padding",
        "section-product-info",
        className,
      ].join(" ")}
    >
      <div className="container">
        <Link to={ROUTES.CATALOG} className="section-product-info__back">
          назад в каталог
        </Link>
        <div className="section-product-info__content">
          <div className="section-product-info__left">
            <Carousel />
          </div>
          <div className="section-product-info__rigth">
            <h2 className="section-product-info__title">Комбинезон</h2>
            <div className="section-product-info__price">
              <div className="section-product-info__price__old">
                {convertNumToPrice(9900)} Р
              </div>
              <div className="section-product-info__price__current">
                {convertNumToPrice(6600)} Р
              </div>
            </div>
            <div className="section-product-info__toggle section-product-info__sizes">
              <div className="section-product-info__toggle__label">Размер</div>
              <div className="section-product-info__toggle__wrapper">
                <button className="section-product-info__toggle__item">
                  <div className="section-product-info__toggle__inner">XS</div>
                </button>
                <button className="section-product-info__toggle__item">
                  <div className="section-product-info__toggle__inner">S</div>
                </button>
                <button className="section-product-info__toggle__item" disabled>
                  <div className="section-product-info__toggle__inner">M</div>
                  <div className="section-product-info__toggle__info">
                    *предзаказ
                  </div>
                </button>
              </div>
            </div>
            <div className="section-product-info__toggle section-product-info__color">
              <div className="section-product-info__toggle__label">Цвет</div>
              <div className="section-product-info__toggle__wrapper">
                <button className="section-product-info__toggle__item">
                  <div
                    className="section-product-info__toggle__inner"
                    style={{ backgroundColor: "#FBC624" }}
                  ></div>
                </button>
                <button className="section-product-info__toggle__item">
                  <div
                    className="section-product-info__toggle__inner"
                    style={{ backgroundColor: "#5FD2E7" }}
                  ></div>
                </button>
                <button className="section-product-info__toggle__item">
                  <div
                    className="section-product-info__toggle__inner"
                    style={{ backgroundColor: "#2F3068" }}
                  ></div>
                </button>
              </div>
            </div>
            <button
              className="section-product-info__basket"
              onClick={dispatch.bind(this, add({ id: 0, title: "product" }))}
            >
              <span>Добавить в корзину</span>
              <svg
                width="26"
                height="26"
                viewBox="0 0 26 26"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <circle cx="13" cy="13" r="13" fill="#5C5681" />
                <path
                  d="M18.1526 8.10639C18.0643 7.88929 17.8954 7.71678 17.6828 7.62662C17.5782 7.58109 17.4659 7.55695 17.3522 7.55554H8.6527C8.42198 7.55554 8.2007 7.64915 8.03756 7.81577C7.87441 7.98238 7.78275 8.20837 7.78275 8.444C7.78275 8.67964 7.87441 8.90562 8.03756 9.07224C8.2007 9.23886 8.42198 9.33246 8.6527 9.33246H15.2556L8.03504 16.6978C7.9535 16.7804 7.88878 16.8786 7.84462 16.9869C7.80045 17.0952 7.77771 17.2113 7.77771 17.3286C7.77771 17.4459 7.80045 17.562 7.84462 17.6703C7.88878 17.7785 7.9535 17.8768 8.03504 17.9594C8.11591 18.0427 8.21213 18.1088 8.31814 18.1539C8.42415 18.199 8.53786 18.2222 8.6527 18.2222C8.76755 18.2222 8.88126 18.199 8.98727 18.1539C9.09328 18.1088 9.1895 18.0427 9.27037 17.9594L16.4823 10.5852V17.3286C16.4823 17.5642 16.5739 17.7902 16.7371 17.9568C16.9002 18.1235 17.1215 18.2171 17.3522 18.2171C17.5829 18.2171 17.8042 18.1235 17.9674 17.9568C18.1305 17.7902 18.2222 17.5642 18.2222 17.3286V8.444C18.2208 8.3279 18.1971 8.2132 18.1526 8.10639Z"
                  fill="#F8F8F8"
                />
              </svg>
            </button>
            <div className="section-product-info__dolyami">
              <span>4 платежа по 3.300 ₽</span>
            </div>
            <div className="section-product-info__desc">
              <ToggleHeaderAuto
                initOpen={true}
                title="Описание"
                desc="Но явные признаки победы институционализации, инициированные исключительно синтетически, превращены в посмешище, хотя само их существование приносит несомненную пользу обществу. Противоположная точка зрения подразумевает, что предприниматели в сети интернет"
              />
              <ToggleHeaderAuto
                title="Состав"
                desc="Но явные признаки победы институционализации, инициированные исключительно синтетически, превращены в посмешище, хотя само их существование приносит несомненную пользу обществу. Противоположная точка зрения подразумевает, что предприниматели в сети интернет"
              />
              <ToggleHeaderAuto
                title="Уход"
                desc="Но явные признаки победы институционализации, инициированные исключительно синтетически, превращены в посмешище, хотя само их существование приносит несомненную пользу обществу. Противоположная точка зрения подразумевает, что предприниматели в сети интернет"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
