import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from "pure-react-carousel";
import { useMediaQuery } from "usehooks-ts";

import { ProductCartVertical } from "~entities/product";

import { SVG_ARROW_LEFT, SVG_ARROW_RIGHT } from "~shared/lib";

export default function SectionRecommendedProduct({
  className,
  title = "Рекомендуем",
}) {
  const matches = useMediaQuery("(min-width: 768px)");

  return (
    <section
      className={[
        "section",
        "section-padding",
        "section-recommended",
        className,
      ].join(" ")}
    >
      <h2 className="section__title section__title--small container">
        {title}
      </h2>
      <CarouselProvider
        className="container"
        naturalSlideWidth={100}
        naturalSlideHeight={100}
        totalSlides={5}
        visibleSlides={matches ? 3 : 2}
        isIntrinsicHeight={true}
      >
        <Slider>
          <Slide index={0}>
            <ProductCartVertical
              imgSrc={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"}
              name="комбинезон"
              price={6600}
            />
          </Slide>
          <Slide index={1}>
            <ProductCartVertical
              imgSrc={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"}
              name="комбинезон"
              price={6600}
            />
          </Slide>
          <Slide index={2}>
            <ProductCartVertical
              imgSrc={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"}
              name="комбинезон"
              price={6600}
            />
          </Slide>
          <Slide index={3}>
            <ProductCartVertical
              imgSrc={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"}
              name="комбинезон"
              price={6600}
            />
          </Slide>
          <Slide index={4}>
            <ProductCartVertical
              imgSrc={process.env.REACT_APP_PUBLIC_URL + "/image-3.png"}
              name="комбинезон"
              price={6600}
            />
          </Slide>
        </Slider>
        <div className="carousel__controls">
          <ButtonBack>{SVG_ARROW_LEFT}</ButtonBack>
          <DotGroup showAsSelectedForCurrentSlideOnly={true} />
          <ButtonNext>{SVG_ARROW_RIGHT}</ButtonNext>
        </div>
      </CarouselProvider>
    </section>
  );
}
